import React, { PureComponent } from 'react'
import { PdfMultiViewer } from 'react-pdfjs-multi'
import { BackTop } from 'antd'

import 'react-pdfjs-multi/dist/react-pdfjs-multi.css'
import './ProjProfessional.css'


const pdfFiles = [
  {
    title: 'LINC Fairview Height',
    source: `${process.env.PUBLIC_URL}/image/FairviewHeight.pdf`,
  },
  {
    title: 'SRO Hartford Villa',
    source: `${process.env.PUBLIC_URL}/image/HartfordVilla.pdf`,
  },
];

export default class ProjectsProf extends PureComponent {
  render() {
    return (
      <div className="projects-prof">
        <div className="projects-prof-top"></div>
        <PdfMultiViewer autoZoom={false} zoom={100} pdfs={pdfFiles} />
        <BackTop />
      </div>
    );
  }
}


